import { formatDuration } from "@/lib/utils";
import { Timer, Play, Pause, Square, RotateCcw } from "lucide-react";
import { TIMER_STATUS_CONFIG } from "@/constants/timer-theme";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";
import { TimerSettings } from "@/components/TimerSettings";
import { TimerHistory } from "@/components/TimerHistory";
import { BasicTimerHistory } from "@/types/timer";

export interface BasicTimerViewProps {
  duration: number;
  remainingTime: number;
  isRunning: boolean;
  isPaused: boolean;
  sessionLabel: string;
  history: BasicTimerHistory[];
  onSessionLabelChange: (value: string) => void;
  onDurationChange: (duration: number) => void;
  onStart: () => void;
  onPause: () => void;
  onStop: () => void;
  onReset: () => void;
  onDeleteHistoryEntry: (id: string) => void;
  onClearHistory: () => void;
}

export const BasicTimerView: React.FC<BasicTimerViewProps> = ({
  duration,
  remainingTime,
  isRunning,
  isPaused,
  sessionLabel,
  history,
  onSessionLabelChange,
  onDurationChange,
  onStart,
  onPause,
  onStop,
  onReset,
  onDeleteHistoryEntry,
  onClearHistory,
}) => {
  const progress =
    duration > 0 ? ((duration - remainingTime) / duration) * 100 : 0;
  const isCompleted = remainingTime === 0 && !isRunning;

  const getStatusBadge = () => {
    if (isRunning) return TIMER_STATUS_CONFIG.running.badgeVariant;
    if (isPaused) return TIMER_STATUS_CONFIG.paused.badgeVariant;
    if (isCompleted) return TIMER_STATUS_CONFIG.completed.badgeVariant;
    return TIMER_STATUS_CONFIG.idle.badgeVariant;
  };

  const getStatusText = () => {
    if (isRunning) return TIMER_STATUS_CONFIG.running.label;
    if (isPaused) return TIMER_STATUS_CONFIG.paused.label;
    if (isCompleted) return TIMER_STATUS_CONFIG.completed.label;
    return TIMER_STATUS_CONFIG.idle.label;
  };

  const statusBadgeVariant = getStatusBadge();
  const statusText = getStatusText();

  return (
    <div className="w-full space-y-6">
      <Card className="w-full">
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle className="flex items-center gap-2">
              <Timer className="h-6 w-6" />
              
            </CardTitle>
            <Badge variant={statusBadgeVariant}>{statusText}</Badge>
          </div>
        </CardHeader>

        <CardContent className="space-y-6">
          <div className="space-y-2">
            <Label htmlFor="session-label" className="text-sm font-medium">
              
            </Label>
            <Input
              id="session-label"
              placeholder=": "
              value={sessionLabel}
              onChange={(e) => onSessionLabelChange(e.target.value)}
              disabled={isRunning}
              className="text-center"
            />
          </div>

          <div className="text-center space-y-4">
            <div className="timer-display-digit font-mono font-bold tracking-wider">
              {formatDuration(remainingTime)}
            </div>

            <div className="space-y-2">
              <Progress value={progress} className="h-3" />
              <div className="flex justify-between text-sm text-muted-foreground">
                <span> {formatDuration(duration)}</span>
                <span>{Math.round(progress)}% </span>
              </div>
            </div>
          </div>

          <div className="flex flex-wrap justify-center gap-3 sm:flex-nowrap">
            {!isRunning ? (
              <Button
                onClick={onStart}
                size="lg"
                disabled={remainingTime === 0}
                className="px-4 sm:px-8"
              >
                <Play className="mr-2 h-5 w-5" />
                {isPaused ? "" : ""}
              </Button>
            ) : (
              <Button
                onClick={onPause}
                variant="outline"
                size="lg"
                className="px-4 sm:px-8"
              >
                <Pause className="mr-2 h-5 w-5" />
                
              </Button>
            )}

            <Button
              onClick={onStop}
              variant="destructive"
              size="lg"
              disabled={!isRunning && !isPaused}
              className="px-4 sm:px-8"
            >
              <Square className="mr-2 h-5 w-5" />
              
            </Button>

            <Button
              onClick={onReset}
              variant="outline"
              size="lg"
              className="px-4 sm:px-8"
            >
              <RotateCcw className="mr-2 h-5 w-5" />
              
            </Button>
          </div>

          <div className="flex flex-wrap justify-center gap-3 pt-2 border-t sm:flex-nowrap">
            <TimerSettings
              duration={duration}
              onDurationChange={onDurationChange}
              disabled={isRunning}
            />

            <TimerHistory
              history={history}
              onDeleteEntry={onDeleteHistoryEntry}
              onClearHistory={onClearHistory}
            />
          </div>
        </CardContent>
      </Card>

      {history.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle className="text-lg"></CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              {history.slice(0, 3).map((entry) => {
                const efficiency =
                  entry.duration > 0
                    ? (entry.actualDuration / entry.duration) * 100
                    : 0;
                return (
                  <div
                    key={entry.id}
                    className="p-3 bg-muted rounded-lg space-y-2"
                  >
                    <div className="flex items-center justify-between">
                      <span className="text-sm font-medium truncate">
                        {entry.label}
                      </span>
                      <Badge
                        variant={entry.completed ? "secondary" : "outline"}
                        className="text-xs"
                      >
                        {entry.completed ? "" : ""}
                      </Badge>
                    </div>
                    <div className="text-xs text-muted-foreground">
                      {formatDuration(entry.actualDuration)} /{" "}
                      {formatDuration(entry.duration)}
                    </div>
                    <div className="text-xs">
                      {" "}
                      <span
                        className={
                          efficiency <= 100
                            ? "text-green-600"
                            : "text-orange-600"
                        }
                      >
                        {Math.round(efficiency)}%
                      </span>
                    </div>
                  </div>
                );
              })}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
};
